import React from 'react'
import { Pagination } from 'react-bootstrap';

function PaginationPage({ len, paginate, nextPage, prevPage, firstPage, lastPage, currentPage }) {
	const pageNumber = [];
	const delta = 1.5;
	let left = currentPage - delta,
	right = currentPage + delta+1;

	for(let i = 0; i < len; i++) {
		if (i === 0 || i === len || i >= left && i < right) {
			pageNumber.push(i);
		}
	}
	
	return (
		<div className='pagination-news'>
			<Pagination >
				<Pagination.First onClick={() => { firstPage() }} />
				<Pagination.Prev onClick={() => { prevPage() }} />
				{
					pageNumber && pageNumber.map((item, index) => (
						<Pagination.Item active={currentPage === item && 'active'} onClick={() => { paginate(item) }} key={index}>{item + 1}</Pagination.Item>
					))
				}	
				<Pagination.Next onClick={() => { nextPage() }} />
				<Pagination.Last onClick={() => { lastPage() }} />
			</Pagination >
		</div>
	)
}

export default PaginationPage;
